import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUi from 'element-ui'
import VueQriously from 'vue-qriously'
import 'babel-polyfill'
import http from './http'
import $ from 'jquery'
import 'element-ui/lib/theme-chalk/index.css'


import ClipboardJS from '../static/js/clipboard.min.js'


Vue.prototype.$ClipboardJS = ClipboardJS

// 原型变量
Vue.prototype.projectName = '/qdsht/'

// dev
// Vue.prototype.appUrl = 'http://192.168.1.244:8080/'

// dist

Vue.prototype.rsaEncrypt = function(obj) {
    var userInfo = JSON.parse(sessionStorage.getItem('userInfo'));
    // if(!userInfo){
    //     alert('非法登入，请重新登陆');
    //     window.location.href='/cms/#/'
    // }
    var keyValue = userInfo.rsaPubKey;
    var encrypt = new JSEncrypt();
    encrypt.setPublicKey(keyValue);
    var objStr = JSON.stringify(obj);
    var strjm = "";
    for (var i = 0; i <= parseInt(objStr.length / 117); i++) {
        var subStr = objStr.substr(i * 117, 117);
        var subJm = encrypt.encrypt(subStr).replace(/\+/g, '%2B');
        strjm = strjm + subJm;
    }
    return strjm;
}
// 计算签名
const md5_key = 12345678;

Vue.prototype.sign = function(_para) {
	let sinded_para = {};
	let para_arr = [];
	let md5_sign_string = "";

	for (let i in _para) {
		para_arr.push((i + "=" + _para[i]));
	}
	para_arr.sort();

	para_arr.push('key=' + md5_key);
	md5_sign_string = md5(para_arr.join('&'));

	for (let s in para_arr) {
		let para_key = para_arr[s].split('=')[0];
		let para_value = para_arr[s].split('=')[1];
		if (para_key != 'key') {
			sinded_para[para_key] = para_value;
		}
	}
	sinded_para.sign = md5_sign_string;

	return sinded_para;
}


Vue.config.productionTip = false

/* eslint-disable no-new */
Vue.use(ElementUi)
Vue.use(VueQriously)
var vm = new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})


