/**
 * http配置
 */
// 引入axios以及element ui中的loading和message组件
import axios from 'axios'
import { Loading, Message, MessageBox } from 'element-ui'
// 超时时间
axios.defaults.timeout = 60000
    // http请求拦截器
var loadinginstace
axios.interceptors.request.use(config => {
        // element ui Loading方法
        //   loadinginstace = Loading.service({ fullscreen: true })
        return config
    }, error => {
        //   loadinginstace.close()
        Message.error({
            message: '加载超时'
        })
        return Promise.reject(error)
    })
    // http响应拦截器
axios.interceptors.response.use(data => { // 响应成功关闭loading
    if (data.data.result == "failure") {
        MessageBox.alert(data.data.msg, '提示', {
            confirmButtonText: '确定',
            type: 'warning',
            center: true
        }).then(() => {
            window.location.href = "index.html#/login"
        }).catch(() => {

        });
    } else if (data.data.result == "repetition") {
        MessageBox.alert('异地登陆,请确认是否本人登陆？否则请修改密码', '提示', {
            confirmButtonText: '确定',
            type: 'warning',
            center: true
        }).then(() => {
            window.location.href = "index.html#/accountLogged"
        }).catch(() => {

        });
    } else if (data.data.result == "PermissionsFail") {
        Message.error({
            message: data.data.msg
        })
    }
    return data
}, error => {
    Message.error({
        message: '加载失败'
    })
    return Promise.reject(error)
})

export default axios


