import Vue from 'vue'
import Router from 'vue-router'

const home = () =>
    import ("@/components/home");
const login = () =>
    import ("@/components/login");
//产品管理
const productManage = () =>
    import ("@/components/prod/productManage");
//用户管理
const userManage = () =>
    import ("@/components/user/userManage");
 //人员管理
const personManage = () =>
import ("@/components/person/personManage");
//客户管理
const clientManage = () =>
    import ("@/components/client/clientManage");
//提现审核
const withdrawal_audit = () =>
import ("@/components/withdrawalAudit/withdrawal_audit");  
      

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/home',
            component: home,
            children: [
                {
                    path: "/productManage",
                    component: productManage
                },                
                {
                    path: "/userManage",
                    component: userManage
                },
                {
                    path: "/personManage",
                    component: personManage
                },
                {
                    path: "/clientManage",
                    component: clientManage
                },
                {
                    path: "/withdrawal_audit",
                    component: withdrawal_audit
                }
                
            ]
        },
        {
            path: "/",
            name: "logindefault",
            component: login
        },
        {
            path: "/login",
            name: "login",
            component: login
        },
        {
            path: "/home",
            name: "home",
            component: home
        }
    ]
})


