<template>
  <div id="app">
    <router-view id="mainView"/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="scss">
*{
  margin: 0;
  padding: 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body,html,#mainView,#app{
  width: 100%;
  height: 100%;
}
.red{
  color: red;
}
.el-table--border th{
  background: aliceblue!important;
}
.el-dialog__header{
  background: #00CDCD;
  .el-dialog__title{
    position: relative;
    top: -3px;
    color: #fff;
  }
  .el-dialog__headerbtn .el-dialog__close{
    color: #fff;
  }
}
.shortingPadding{
  padding: 5px 0px!important;
}
.table_container{
  position:relative;
}
.clear10{
  width: 100%;
  height: 10px;
  clear: both;
}
.el-message{
  top: 50%!important;
}
.el-tabs--card>.el-tabs__header .el-tabs__nav{
  border-left: 0!important;
  border-top: 0;
}
.el-transfer-panel .el-transfer-panel__header .el-checkbox .el-checkbox__label{
  width: 90%;
}
.el-table__body tr.current-row>td{
  background-color:lightblue !important;
}
.nav {
  width: 100%;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 5px;
  padding-top: 4px;
  background: #ffffff;
  position: absolute;
  top: 0px;
  z-index: 9;
  .el-breadcrumb {
    padding: 20px 0px 0px 10px;
    width: 30%;
    float: left;
    box-sizing: border-box;
  }
  .searchGroup {
    width: 70%;
    height: 50px;
    float: right;
    overflow: hidden;
    .search-button {
      padding-right: 5px;
    }
    button {
      width: 100%;
    }
    .el-date-editor {
      width: 100%;
    }
  }
}
.el-tabs__content{
  width: 100%;
  height: 100%;
  overflow-y: auto;
}
.el-tabs__header{
  margin: 0!important;
  padding: 0!important;
}
.el-table__expanded-cell[class*=cell]{
  padding: 0!important;
}
.el-message-box__message p{
  word-break:break-all;
}
.el-collapse-item__header{
  background: #f2f2f2;
  text-indent: 10px!important;
  margin-bottom: 8px;
}
.el-collapse-item__header.is-active{
  background: #f2f2f2;
}
.el-table{
  color: #333!important;
}
.el-table thead{
  color: #000!important;
}
.el-table td, .el-table th.is-leaf{
  // border-bottom:1px solid #ccc;
  // border-right:1px solid #ccc;
  border-color: #AEEEEE!important;
}
.el-input.is-disabled .el-input__inner{
  color: red!important;
}
.el-table td, .el-table th{
  padding: 10px 0px!important;
}
</style>



